/* NAV */
.Nav.container {
  height: 30%;
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Nav {
  flex: 1;
  display: flex;

  /* align-items: center; */
  /* justify-content: space-between; */
  /* padding: 0 2rem; */
  width: 100vw;
  border-radius: 1.5rem;
  color: white;
  background: #1db954;
  box-shadow: inset 2px 2px 5px #116f32, inset -2px -2px 5px #a5e3bb;
}

.Nav:hover {
  /* border-radius: 20px; */
  box-shadow: inset 2px 2px 5px #116f32, inset -2px -2px 5px #a5e3bb;
}

.Nav a {
  color: white;
  display: inline-block;
  padding: 2.5rem;
  margin: 0.5rem;
}

.Nav a:hover {
  background: rgba(255, 255, 255, 0.6);
  border-radius: 35px;
}

.moodmix {
  font-family: "Righteous", cursive;
  font-size: 7.8rem;
  text-shadow: 0 9px 7px rgba(0, 0, 0, 0.8);
}

.logo.title.nav-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  /* justify-content: center; */
  /* align-items: left; */
  /* align-items: center; */
  /* margin: 1rem; */
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 1rem 2rem;
}

/* FORM */
.form-container {
  display: flex;
  justify-content: flex-end;
}

/* SEARCHBAR */
.search-wrapper.green-search-wrapper {
  flex: 1;
  /* display: grid; */
  background-color: #1db954;
}

.searchbar-container.green-searchbar-container {
  /* border: 1px solid red; */
}

.searchbar.green-searchbar {
  box-shadow: inset 7px 7px 7px #116f32, inset -8px -8px 9px #77d598;
  color: #ffffff;
}
.searchbar:hover.green-searchbar:hover {
  box-shadow: inset 9px 9px 3px #116f32, inset -8px -8px 8px #77d598;
}
.searchbar:focus.green-searchbar:focus {
  box-shadow: inset -8px -8px 6px #77d598, inset 8px 8px 3px #116f32;
}

/* SEARCH BTN */
.green-btn-container.btn-container {
  border: 0.01rem solid #77d598;
  border: 0.01rem solid rgba(0, 0, 0, 0.1);
  background-color: #1db954;
}
.green-btn-container.btn-container:hover {
  box-shadow: 0 0 5px #0f5d2a, 0 0 5px #bbeacc;
}
.green-btn.btn {
  border: 1px solid #77d598;
  border: 0.01rem solid rgba(0, 0, 0, 0.1);

  background-color: #1db954;
  box-shadow: 1rem 1rem 2rem #116f32, inset -2rem -2rem 1rem #61ce87;
}
.green-btn:hover.btn:hover {
  box-shadow: 0 0 5px #0f5d2a, 0 0 3px #bbeacc;
}
.green-btn:active.btn:active {
  /* LESS GAUDY: */
  /* box-shadow: inset 1rem 1rem 2rem #116f32, -2rem -2rem 3rem #77d598; */
  box-shadow: inset 2rem 2rem 1rem #116f32, inset -2rem -2rem 1rem #77d598;
}

.DropdownMenu {
  width: 100%;
  height: 100%;
  margin: 1rem;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5), -2px -2px 5px rgba(0, 0, 0, 0.5);
}

.Select {
  /* height: 100%; */
  /* background: none; */
  /* box-shadow: inset 7px 7px 7px #cbced1, inset -8px -8px 9px #ffffff; */
  width: 100%;
  border-radius: 5px;
  border: none;
  border: 1px solid rgba(0, 0, 0, 0.5);
  outline: none;
  background-color: #1db954;
  text-align: center;
}
.option {
  width: 90%;
  background-color: red;
}

