/* FONTS */

/*  RESETS */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  font-size: 36px;
  font-family: "Spartan", sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

html {
  font-size: 10px;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  cursor: pointer;
}

